/* .......SERVICE- 1..... */

.service1 img {
  width: 100%;
}

/* .......FRESH - 2...... */

.fresh2 {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  padding: 40px 20px;
}

.fresh2 .fresh2-cards {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
  padding: 40px 20px;
}

.fresh2 .fresh2-cards .card1 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.fresh2 .fresh2-cards .card1 .content {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  text-align: center;
  width: 180px;
  height: 80px;
  background-color: white;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  position: absolute;
  top: 210px;
  border-radius: 10px;
  padding: 10px;
}

.fresh2 .fresh2-cards .card1 .content img {
  width: 50px;
  position: absolute;
  bottom: 60px;
}

.fresh2 .fresh2-cards .card1 .content button {
  border: 1px solid #4baf47;
  background-color: #4baf47;
  width: 80px;
  height: 30px;
  border-radius: 10px;
  position: absolute;
  left: 100px;
}

.fresh2 .fresh2-cards .img img {
  width: 200px;
}
