/* ......FRESH - 4..... */

.fresh4 {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 40px 20px;
}

.fresh4 .fresh4-img {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s all;
}

.fresh4 .fresh4-img img {
  width: 300px;
}

.fresh4 .fresh4-content h5 {
  font-size: 20px;
  color: #eec044;
  font-family: "Covered By Your Grace", cursive;
  font-weight: 400;
  font-style: normal;
}

.fresh4 .fresh4-content {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  gap: 15px;
}

.fresh4 .fresh4-content p {
  font-size: 12px;
}

.fresh4 .fresh4-content .card {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
  transition: 0.5s all;
}

.fresh4 .fresh4-content .card img {
  width: 100px;
}

